import { Table, Form, Row, Container, Col, InputGroup } from "react-bootstrap";
import { useSelector } from "react-redux";
import styles from "../css/payment.module.css";
import React, { useState, useEffect } from "react";

import { loadTossPayments } from "@tosspayments/payment-sdk";

function CheckOut() {
  let [pay, setPay] = useState(0);
  let item = useSelector((state) => {
    return state.cartItem;
  });
  let user = useSelector((state) => {
    return state.user;
  });
  let price = 0;
  {
    item.map((item) => (price += item.tprice));
  }
  useEffect(() => {
    const clientKey = "test_ck_D5GePWvyJnrK0W0k6q8gLzN97Eoq";
    if (pay == 1) {
      loadTossPayments(clientKey).then((tossPayments) => {
        tossPayments
          .requestPayment("", {
            //   
            //   
            amount: price,
            orderId: "TUfXTCWu5eIkW2mLrFxxH",
            orderName: item[0].name + "" + item.length + "",
            customerName: user.name,
            successUrl: "http://localhost:3000/",
            failUrl: "http://localhost:3000/checkout",
          })
          .catch(function (error) {
            if (error.code === "USER_CANCEL") {
              alert("");
            } else if (error.code === "INVALID_CARD_COMPANY") {
              alert("  ");
            }
          });
      });
    }
    if (pay == 2) {
      loadTossPayments(clientKey).then((tossPayments) => {
        tossPayments
          .requestPayment("", {
            //   
            //   
            amount: price,
            orderId: "TUfXTCWu5eIkW2mLrFxxH",
            orderName: item[0].name + "" + item.length + "",
            customerName: user.name,
            successUrl: "http://localhost:3000/",
            failUrl: "http://localhost:3000/checkout",
            validHours: 24,
            cashReceipt: {
              type: "",
            },
          })
          .catch(function (error) {
            if (error.code === "USER_CANCEL") {
              alert("");
            } else if (error.code === "INVALID_CARD_COMPANY") {
              alert("  ");
            }
          });
      });
    }
  }, [pay]);
  return (
    <>
      <Container className={styles.container}>
        <Row>
          {" "}
          <Table className={styles.table}>
            <tr>
              <th> </th>
            </tr>
            <tr>
              <td></td>
              <td>{user.name}</td>
            </tr>
            <tr>
              <td></td>
              <td>{user.email}</td>
            </tr>
            <tr>
              <td></td>
              <td>{user.tel}</td>
            </tr>
          </Table>
        </Row>
        <Row>
          {" "}
          <Table className={styles.table}>
            <tr>
              <th></th>
            </tr>
            <tr>
              <td></td>
              <td>{user.name}</td>
            </tr>
            <tr>
              <td></td>
              <td>{user.address}</td>
            </tr>
            <tr>
              <td></td>
              <td>{user.tel}</td>
            </tr>
            <tr>
              <td></td>
              <td>
                <InputGroup>
                  <Form.Control as="textarea" aria-label="With textarea" />
                </InputGroup>
              </td>
            </tr>
          </Table>
        </Row>
        <Row>
          <Table className={styles.table}>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            {item.map((item) => (
              <tr>
                <td>{item.name}</td>
                <td>{item.count}</td>
                <td>{item.tprice}</td>
              </tr>
            ))}
          </Table>
        </Row>
        <Row>
          {" "}
          <Table className={styles.table}>
            <tr>
              <th></th>
            </tr>
            <tr>
              <td></td>
              <td>{price}</td>
            </tr>
            <tr>
              <td></td>
              <td>0</td>
            </tr>
            <tr>
              <td></td>
              <td>2500</td>
            </tr>
            <tr>
              <td></td>
              <td>{price + 2500}</td>
            </tr>
            <tr>
              <td></td>
              <td>
                <Form.Select id="payList" aria-label="paymentList">
                  <option></option>
                  <option value="1"></option>
                  <option value="2"></option>
                </Form.Select>
              </td>
            </tr>
          </Table>
        </Row>{" "}
        <button
          onClick={() => {
            let e = document.getElementById("payList");
            let value = e.options[e.selectedIndex].value;
            setPay(parseInt(value));
          }}
        >
          
        </button>
      </Container>
    </>
  );
}

export default CheckOut;
